// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App'
import router from './router'
import axios from 'axios';
import qs from 'qs';
import {setBaseUrl} from '@/config.js'
import global from '@/utils/global.js'
import Blob from '@/excel/Blob'
import Export2Excel from '@/excel/Export2Excel.js'
import store from './store'

// axios.defaults.baseURL = "http://zhuxiongfeng.cn";
axios.defaults.baseURL = "http://localhost:8080/Bicycle";
// axios.defaults.baseURL = "http://192.168.1.119:80"
//引入element-ui控件
Vue.config.productionTip = false
//使用use命令才起作用
Vue.use(ElementUI)

Vue.prototype.$event = new Vue();
Vue.prototype.$qs = qs;
Vue.prototype.axios = axios;
Vue.prototype.$baseUrl=setBaseUrl;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



