<template>
<el-container style="height:722px" class="app-wrapper">

  <Sidebar/>
  
  <el-container>
    
    <el-header class="layout_header" style="padding: 0%">
    <Navbar/>
    </el-header>

    <el-main>
    <AppMain style="text-align:center;"/>
    </el-main>
  
  </el-container>
</el-container>
</template>

<script>
import {Navbar,Sidebar,AppMain} from './components'
import {mapState} from 'vuex'
  export default {
    components:{
      Navbar,
      Sidebar,
      AppMain
    },
    methods:{

   },
    mounted(){
      //监听页面变化实现左边路由界面缩放变化
      var docWith = document.body.clientWidth;
       window.onresize = () => {
         var docWith2 = document.body.clientWidth;
         if(docWith2<1072){
           this.$event.$emit('dataFrom',true);
         }
       }
    }
  }
</script>
<style>
  /*
  
  .el-aside {
    color: #333;
    height: 100%;
    background-color: #545c64;
  }
  .el-submenu .el-menu-item{
    min-width:0px;
  }
  .el-menu{
    border-right: 0px;
  } */
   .layout_header {
    padding: 0%;
  }
  .app-wrapper {
    position: relative;
    height: 100%;
    width: 100%;
  }
 #app{
    margin-top: 0px;
    text-align:left;
  }
</style>


