import Vue from 'vue'
import Vuex from 'vuex'
import footerStatus from './modules/footerStatus'
import collection from './modules/collection'
import left from './modules/left'
import user from './modules/user'

Vue.use(Vuex)

export default new Vuex.Store({
    modules:{
        footerStatus,
        collection,
        left,
        user
    }
})


