const left={
    state:{
        isCollapse:false,//初始化一个colects数组
    },
    getters:{
        renderCollapse(state){//承载变化的collects
            return state.isCollapse;
        }
    },
    mutations:{
        changestatus(state){//如何变化collects，插入items
            state.isCollapse = true;
        }
    },
    actions:{
        invokeStatus(context){//触发mutations里面的pushCollects ,传入数据形参item 对应到items
            context.commit('changestatus');
        }
    }
}
export default left


