<template>
<el-menu background-color="#545c64">
      <sidebar-item v-for="item in routes" :key="item.path" :item="item" :base-path="item.path"  :isCollapse="isCollapse"/>
</el-menu>
</template>

<script>
import SidebarItem from './Sidebaritem'
import { mapState } from 'vuex'
export default {
  components: { SidebarItem},
  data(){
    return {
      routers: null,
      isCollapse:false
    }
  },
  methods: {
    hasItem(list,item){
      return list.filter(it => {
        return it.name == item.name
      }).length > 0
    },
    show(){
      console.log("t:",this.routes);
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  computed: {
    routes() {
      let routers = JSON.parse(JSON.stringify(this.$router.options.routes))
      return routers
    },
    // ...mapState(['renderCollapse'])
  },
  mounted(){
    this.$event.$on('dataFrom', data => this.isCollapse = data);
  },
  beforeDestroy(){
    this.$event.$off('dataFrom');
  },
  created(){
    this.isCollapse = false;
  }
}
</script>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  background-color: #545c64;
}
</style>



