import Vue from 'vue'
import Router from 'vue-router'
//使用懒加载的方式来引入,只有路由被访问的时候才加载
import home from '@/components/home'
import store from '../store/index'
const loginpage = resolve => require(['@/components/Login'],resolve)
const register = resolve => require(['@/components/register'],resolve)

Vue.use(Router)

import Layout from '../views/layout/layout'
import AppMain from '../views/layout/components/AppMain'

const VueRouter = new Router({
  routes:[
    {
      path: '/',
      name: 'login',
      hidden: true,
      component: loginpage
    },
    {
      path: '/login',
      name: 'login',
      hidden: true,
      component: loginpage
    },
    {
      path: '/reg',
      name: 'reg',
      hidden: true,
      component: register
    },
    //个人信息
    {
      path: '/user',
      component: Layout,
      name: 'user',
      meta: {title: '个人信息',icon: 'el-icon-message-solid'},
      children: [
        {
          path: 'userinfo',
          name: 'userinfo',
          component: () => import('@/views/user/userInfo.vue'),
          meta: { title: '我的信息', auth: true }
        },
        {
          path: 'change',
          name: 'change',
          hidden:true,
          component: () => import('@/views/user/changeInfo.vue'),
          meta: { title: '修改信息', auth: true }
        }
    ]
    },
    //用户
    {
      path: '/bicycle',
      component: Layout,
      name: 'bicycle',
      meta: {title: '用户',icon: 'el-icon-user-solid'},
      children: [
        {
          path: 'list',
          name: 'list',
          component: home,
          meta: { title: '可租车', auth: true }
        },
       {
         path:'rent',
         name:'rent',
         component: () => import('@/views/user/rentcar.vue'),
         meta: { title: '已租车' , auth: true}
       },
       {
        path:'rentDetail',
        name:'rentDetail',
        component: () => import('@/views/user/rentDetail.vue'),
        meta: { title: '正在租车记录' , auth: true}
      },
      {
        path:'rentedRecord',
        name:'rentedRecord',
        component: () => import('@/views/user/rentedRecord.vue'),
        meta: { title: '已租车记录' , auth: true}
      }
    ]
    },
    
    //管理
    {
      path:'/admin',
      component: Layout,
      name: 'admin',
      hidden:true,
      meta:{title: '管理',icon: 'el-icon-s-platform'},
      children:[
        {
          path: 'userList',
          name: 'userList',
          component: () => import("@/views/admin/userList.vue"),
          meta: { title: '用户列表', auth: true }
        },
        {
          path: 'bicycleList',
          name: 'bicycleList',
          component: () => import("@/views/admin/adminRent.vue"),
          meta: {title: '单车列表'}
        },
        {
          path: 'import',
          name: 'import',
          hidden:true,
          component: () => import("@/views/admin/importBicycle.vue"),
          meta: {title: '导入单车'}
        },
      ]
    }
]
})
//路由全局判断
VueRouter.beforeEach((to, from,next) => {
  let res = store.state.user.accessToken;
  if(to.path!='/login'&&to.path!="/"&&to.path!="/reg"){
    console.log("res:",store.state.user.accessToken) 
    if(res){
      if(res.level==1){//等级为管理员
        // console.log(JSON.parse(JSON.stringify(VueRouter.options.routes))[4].hidden);
        VueRouter.options.routes[5].hidden=false//等级为管理员时显示管理路由
        // console.log(JSON.parse(JSON.stringify(VueRouter.options.routes))[4].hidden);
        }else{
          VueRouter.options.routes[5].hidden=true
        }
        next();
    }else{
       next('login') 
    } 
  
  }else{
    next()
  }
  

})
export default VueRouter




