<template>

      <el-header style=" font-size: 12px;" class="navbar">
         <el-radio-group v-model="isCollapse" style="width:50px;" size="mini"  :change="sendData()">
            <el-radio-button :label="false">展开</el-radio-button>
            <el-radio-button :label="true" >收起</el-radio-button>
          </el-radio-group>
        <el-dropdown trigger="click">
          <span class="avatar-wrapper">
        {{name==null?"用户":name}}
        <!-- <i class="el-icon-setting" style="margin-right: 40px"></i> -->
      </span>
        <el-dropdown-menu slot="dropdown">
          <!-- <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>修改密码</el-dropdown-item> -->
          <el-dropdown-item>
            <span @click="outLogin">退出登录</span>
            </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      </el-header>
</template>

<script>
export default {
  name: 'Navbar',
  data(){
    return {
      isCollapse:false,
      name:''
    }
  },
  methods:{
    sendData(){
      this.$event.$emit('dataFrom',this.isCollapse);
    },
    outLogin(){
      console.log("aaa");
      
      this.$store.dispatch("removeItem");
      this.$router.push("/login");
    }
  },
  mounted(){
    this.$event.$on('dataFrom', data => this.isCollapse = data);
  },
  beforeDestroy(){
    this.$event.$off('dataFrom');
  },
  created(){
    this.name = this.$store.state.user.accessToken.name;
  }
}
</script>
<style scoped>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
    position: relative;
    border-bottom: 1px solid #e6e6e6;
    padding: 0;
  }
  .navbar {
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  padding-right: 0;
}
.avatar-wrapper {
      cursor: pointer;
}
.el-card__body {
  padding: 0;
}
.el-dropdown{
  margin-top: 5px;
  right: 55px;
  position: absolute;
}
.el-radio-group{
  width: 55px;
}

</style>
<style>
.el-radio-button__inner {
    height: 30px;
}
.el-radio-button:first-child .el-radio-button__inner {
  border-left: 0px;
}
</style>


