const user = {
    state:{
        accessToken: JSON.parse(localStorage.getItem("access-token")),
    },
    mutations:{
        SET_ITEM:(state,value) => {
            let obj={
                id : JSON.parse(value).id,
                phone : JSON.parse(value).phone,
                level : JSON.parse(value).level,
                name : JSON.parse(value).name
            }
            console.log(obj);
            
            state.accessToken = obj;
            localStorage.setItem('access-token',JSON.stringify(obj));
        },
        REMOVE_ITEM:(state) => {
            state.accessToken = '';
            localStorage.removeItem('access-token');
        },
        GET_ITEM:(state) =>{
            if(state.accessToken){
                return state.accessToken;
            }
        }
    },
    actions:{
        setItem({ commit },param){
            commit('SET_ITEM',param);
        },
        removeItem({ commit }){
            commit('REMOVE_ITEM');
        },
        getItem({ commit , state}){
            commit('GET_ITEM');
            // return state.accessToken;
        }
    }
}

export default user


