<template>
    <el-menu
      :default-active="$route.path"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    > 
     
      <el-submenu :index="resolvePath(item.path)" v-if="!item.hidden">
        <template slot="title">
          <i :class="item.meta.icon"></i>
          <span slot="title">{{item.meta.title}}</span>
        </template>

        <el-menu-item-group  v-for="child in item.children" :item="child" :key="child.path" >

          <router-link  v-if="!child.children&&!child.hidden" :to="resolvePath(child.path)">
            <el-menu-item :index="resolvePath(item.path+'/'+child.path)" v-if="!child.hidden">{{child.meta.title}}</el-menu-item>
          </router-link>

          <el-submenu v-else-if="child.children&&!child.hidden" :index="resolvePath(child.path)">
            <template slot="title">
              <span slot="title">{{child.meta.title}}</span>
            </template>
            <el-menu-item-group  v-for="chil in child.children" :item="chil" :key="chil.path">
              <router-link v-if="!chil.hidden" :to="resolvePath(child.path+'/'+chil.path)">
                <el-menu-item :index="resolvePath(child.path+'/'+chil.path)" v-if="!chil.hidden" >{{chil.meta.title}}</el-menu-item>
              </router-link>
            </el-menu-item-group>
          </el-submenu>
          
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
</template>
<script>
import path from "path";
import { validateURL } from '@/utils/validate'
export default {
  name: "Sidebaritem",
  props: {
    item: {
      type: Object,
      required: true
    },
    isNest: {
      type: Boolean,
      default: false
    },
    basePath: {
      type: String,
      default: ''
    },
    isCollapse:{
      type:Boolean,
      default:false
    }
  },
  data() {
    return {
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    resolvePath(routePath) {
      if (this.isExternalLink(routePath)) {
        return routePath;
      }
      return path.resolve(this.basePath, routePath);
    },
    isExternalLink(routePath) {//校验地址
      return validateURL(routePath)
    }
  }
};
</script>
<style scoped>
.el-aside {
  background-color: #545c64;
}
.el-submenu .el-menu-item {
  min-width: 0px;
}
.el-menu {
  border-right: 0px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  background-color: #545c64;
}
.el-menu i {
  color: rgb(191, 203, 217);
}
.el-menu-item-group__title{
  padding: 0;
}
a:-webkit-any-link{
  text-decoration: none;
}
</style>


